.PlayerTracker--Container {
    border: 1px solid black;
    margin: 4px 8px 4px 0px;
}

.PlayerTracker--Name {
    text-align: center;
}

.PlayerTracker--Name button {
    margin-left: 5px;
}

.PlayerTracker--Description {
    display: -ms-flexbox;
