    margin-right: 4px;
    margin-bottom: 4px;
}

.PlayerTracker--XP {
    display: -ms-flexbox;
    display: flex;
}

.PlayerTracker--XP--Icon {
    height: 24px;
    width: 24px;
    display: block;
