body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h5 {
  margin-bottom: 4px;
  margin-top: 8px;
}

@font-face {
  font-family: "PirataOne";
  src: url(./PirataOne-Regular.ttf) format('truetype');
}

@font-face {
  font-family: "SakkalMajalla";
  src: url(./SakkalMajalla.ttf) format('truetype');
}

@font-face {
  font-family: "ZIKUTANGSFXST";
  src: url(./ZIKUTANGSFXST.ttf) format('truetype');
}

@-moz-document url-prefix() {
  button {
    padding: 0;
  }

  /* wtf this doesn't work */
  button::-moz-focus-inner,
  input[type="button"]::-moz-focus-inner,
  input[type="submit"]::-moz-focus-inner,
  input[type="reset"]::-moz-focus-inner {
    padding: 0;
    border-style: none;
  }
}



